.chat{
    display: flex;
    flex-direction: column;
    flex: 75;
    background-color:  #363a3f;
    height: 100vh; 
}
.chat_input{
    color: lightgray;
    display:flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    border-radius: 5px;
    margin: 20px;
    border-top: 20px;
    background-color: #474b53;
}
.chat_input>form>input{
    padding: 15px;
    background: transparent;
    border: none;
    width: 100%;
    outline-width: 0;
    color: white;
    font-size: large;
}
.chat_input>form{
    flex: 1;
}
.chat_button{
    display: none;
}
.chat_inputIcon >.MuiSvgIcon-root {
    padding: 5px;
} 
.chat_messages{
 flex: 1;
}